import { getSessionStorageKey } from '../init-config';
const cloudLocaleMap = {
    zh: 'zh-CN',
    en: 'en-US',
};
/**
 * 将字节云的 locale 转换为通用 locale
 */
const cloudLocaleToNormalLocale = (cloudLocale) => {
    return (cloudLocaleMap[cloudLocale] || cloudLocaleMap.zh);
};
